import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const router = new VueRouter({
    /* vinculem al projecte */
  base: '/miau/',
  routes: [
    {
      // pagina inicial al obrir la pagina web
      path: '/',
      name: 'home',
      component: HomeView
    },
    {
      // enllaç a la subpagina per a que funcioni el menu
      path: '/home',
      name: 'home',
      component: HomeView
    },
    {
      // enllaç a la subpagina per a que funcioni el menu
      path: '/about',
      name: 'about',
      component: () => import( '../views/AboutView.vue')
    },
    {
      // enllaç a la subpagina per a que funcioni el menu
      path: '/adopta',
      name: 'adopta',
      component: () => import( '../views/AdoptaView.vue')
    },
    {
      // enllaç a la subpagina per a que funcioni el menu
      path: '/galeria',
      name: 'galeria',
      component: () => import( '../views/GaleriaView.vue')
    },
    {
      // enllaç a la subpagina per a que funcioni el menu
      path: '/registre',
      name: 'registre',
      component: () => import( '../views/RegistreView.vue')
    }
  ]
  
})

export default router
