<template>
  <div>
    <!-- carrusel d'imatges amb fletxes als costats, seleccio a sota i sense moviment automatic -->
    <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="0" 
      controls
      indicators
      background="#ababab"
      img-height="25%"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
    >
      <b-carousel-slide img-src="https://images.pexels.com/photos/617278/pexels-photo-617278.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1" alt="imatge generica"></b-carousel-slide>
      <b-carousel-slide img-src="https://images.pexels.com/photos/11919075/pexels-photo-11919075.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1" alt="imatge generica"></b-carousel-slide>
      <b-carousel-slide img-src="https://images.pexels.com/photos/13585816/pexels-photo-13585816.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1" alt="imatge generica"></b-carousel-slide>
      <b-carousel-slide img-src="https://images.pexels.com/photos/10626222/pexels-photo-10626222.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1" alt="imatge generica"></b-carousel-slide>
      <b-carousel-slide img-src="https://images.pexels.com/photos/12831664/pexels-photo-12831664.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1" alt="imatge generica"></b-carousel-slide>
    </b-carousel>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        slide: 0,
        sliding: null
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      }
    }
  }
</script>
